import Loader from "Components/Loader";
import React, { useState } from "react";
import "./BackgroundVideo.css";

const BackgroundVideo = ({
  width = "100%",
  height = "100%",
  objectFit = "cover",
  top = "-0.5rem",
  video,
  position = "fixed",
}) => {
  const [loaded, setLoaded] = useState("");
  return (
    <div
      className="main col-12 col-md-10 d-flex justify-content-center "
      style={{ position, zIndex: "-1" }}
    >
      {loaded === "" && <Loader />}
      <video
        autoPlay
        loop
        muted
        preload="auto"
        style={{ width, height, objectFit, top }}
        onCanPlayThrough={(e) => setLoaded(e.type)}
      >
        <source src={video} type="video/mp4" />
      </video>
    </div>
  );
};

export default BackgroundVideo;
