import React from "react";
import Header from "Components/Header";
import Home from "Pages/Home";
// TODO:change to browserRoute in production
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import HauteCouture from "Pages/HauteCouture";
import Accessories from "Pages/Accessories";
import Contact from "Pages/Contact";
import WorkClothes from "Pages/WorkClothes";
import Footer from "Components/Footer";
import ResponsiveWrapper from "Components/ResponsiveWrapper";
import NotFound from "Pages/NotFound";

const AppRoutes = () => {
  return (
    <>
      <Router>
        <Header />
        <div className="positionTopPage">
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/haute-couture" component={HauteCouture} />
            <Route exact path="/work-clothes" component={WorkClothes} />
            <Route exact path="/accessories" component={Accessories} />
            <Route exact path="/contact" component={Contact} />{" "}
            <Route path="/*" component={NotFound} />
          </Switch>
        </div>
        <ResponsiveWrapper viewSize="md">
          <Footer />
        </ResponsiveWrapper>
      </Router>
    </>
  );
};

export default AppRoutes;
