import React from "react";
import "./Navbar.css";
import { Link, useLocation } from "react-router-dom";
import { IconRoutesPages } from "Constants/IconRoutesPages.js";

const Navbar = ({ isMobile, height = "3rem" }) => {
  const { pathname } = useLocation();
  return (
    <nav
      className={`styleNavbar d-flex align-items-end justify-content-center ps-1 ps-md-auto text-white w-100 ${
        isMobile && "fixed-bottom pb-1 "
      }`}
      style={{
        height,
        backgroundColor: "var(--primary)",
        overflowX: "auto",
        overflowY: "hidden",
      }}
    >
      <ul
        className={`d-flex w-100 justify-content-around  align-items-center mb-0 ps-0 w-100 ${
          isMobile ? "fs-1  mb-1" : "fs-3"
        }`}
      >
        {IconRoutesPages.map(({ icon, to }) => (
          <Link
            to={to}
            style={
              isMobile && {
                padding: "10px",
                borderRadius: "50%",
                transform: "translateY(0rem)",
                transition: "0s",
              }
            }
            className={`linkNavbar ${
              pathname === to &&
              (isMobile ? " activeIconMobile " : " activeIcon ")
            } `}
          >
            {icon}
          </Link>
        ))}
      </ul>
    </nav>
  );
};
export default Navbar;
