import React from "react";

const ResponsiveWrapper = ({ viewSize, isMobile, children }) => {
  return (
    <>
      <div
        className={`d-${isMobile ? "block" : "none"} d-${viewSize}-${
          isMobile ? "none" : "block"
        } `}
      >
        {children}
      </div>
    </>
  );
};

export default ResponsiveWrapper;
